import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';


// @Injectable()
@Injectable({
  providedIn: 'root'
})
export class DropDownService {

  API_URL: string = environment.APIEndpoint;

  constructor(
    private http: HttpClient,
  ) { }

  getBrandDrp(param=null): Observable<any> {
    return this.http.get(`${this.API_URL}/brand/drp`, {params: param});
  }
  getCategoryDrp(param=null): Observable<any> {
    return this.http.get(`${this.API_URL}/category/drp`, {params: param});
  }
}
